import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect, useState } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack } from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider, RHFSelect } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
DataSelInt.propTypes = {
  discount: PropTypes.object,
};
export default function DataSelInt({ discount }) {
  const isMountedRef = useIsMountedRef();
  const [allvend, SetVend] = useState([{ key: 'Habukhan1', name: '' }]);
  const NewUserSchema = Yup.object().shape({
    mtn_sme: Yup.string().required('MTN SME VENDING REQUIRED'),
    airtel_sme: Yup.string().required('AIRTEL SME VENDING REQUIRED'),
    glo_sme: Yup.string().required('GLO SME VENDING REQUIRED'),
    mobile_sme: Yup.string().required('9MOBILE SME VENDING REQUIRED'),

    mtn_cg: Yup.string().required('MTN CG VENDING REQUIRED'),
    airtel_cg: Yup.string().required('AIRTEL CG VENDING REQUIRED'),
    glo_cg: Yup.string().required('GLO CG VENDING REQUIRED'),
    mobile_cg: Yup.string().required('9MOBILE CG VENDING REQUIRED'),

    mtn_g: Yup.string().required('MTN GIFTING VENDING REQUIRED'),
    airtel_g: Yup.string().required('AIRTEL GIFTING VENDING REQUIRED'),
    glo_g: Yup.string().required('GLO GIFTING VENDING REQUIRED'),
    mobile_g: Yup.string().required('9MOBILE GIFTING VENDING REQUIRED'),

    mtn_sme2: Yup.string().required('MTN SME 2 VENDING REQUIRED'),
    airtel_sme2: Yup.string().required('AIRTEL SME 2 VENDING REQUIRED'),
    glo_sme2: Yup.string().required('GLO SME 2 VENDING REQUIRED'),
    mobile_sme2: Yup.string().required('9MOBILE SME 2 VENDING REQUIRED'),

    mtn_datashare: Yup.string().required('MTN DATASHARE VENDING REQUIRED'),
    airtel_datashare: Yup.string().required('AIRTEL DATASHARE VENDING REQUIRED'),
    glo_datashare: Yup.string().required('GLO DATASHARE VENDING REQUIRED'),
    mobile_datashare: Yup.string().required('9MOBILE DATASHARE VENDING REQUIRED'),
  });
  const defaultValues = useMemo(
    () => ({
      mtn_sme: discount?.mtn_sme || '',
      airtel_sme: discount?.airtel_sme || '',
      glo_sme: discount?.glo_sme || '',
      mobile_sme: discount?.mobile_sme || '',

      mtn_cg: discount?.mtn_cg || '',
      airtel_cg: discount?.airtel_cg || '',
      glo_cg: discount?.glo_cg || '',
      mobile_cg: discount?.mobile_cg || '',

      mtn_g: discount?.mtn_g || '',
      airtel_g: discount?.airtel_g || '',
      glo_g: discount?.glo_g || '',
      mobile_g: discount?.mobile_g || '',

      mtn_sme2: discount?.mtn_sme2 || '',
      airtel_sme2: discount?.airtel_sme2 || '',
      glo_sme2: discount?.glo_sme2 || '',
      mobile_sme2: discount?.mobile_sme2 || '',

      mtn_datashare: discount?.mtn_datashare || '',
      airtel_datashare: discount?.airtel_datashare || '',
      glo_datashare: discount?.glo_datashare || '',
      mobile_datashare: discount?.mobile_datashare || ''
    }),
    [discount]
  );
  const AccessToken = window.localStorage.getItem('accessToken');
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  useEffect(() => {
    if (discount !== undefined) {
      reset(defaultValues);
      SetVend(discount?.habukhan_code)
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [discount]);
  const {
    reset,
    setError,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/edit/datasel/account/${AccessToken}/habukhan/secure`, data)
      swal('success', `Updated`, 'success');
    } catch (error) {
      if (isMountedRef.current) {
        setError('afterSubmit', error);
      }
    }
  };


  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>

        <Grid item xs={12} md={8}>
          {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>

            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >
              <RHFSelect name="mtn_sme" label="MTN SME" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>

              <RHFSelect name="airtel_sme" label="AIRTEL SME" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="glo_sme" label="GLO SME" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="mobile_sme" label="9MOBILE SME" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>

              <RHFSelect name="mtn_cg" label="MTN CG" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>

              <RHFSelect name="airtel_cg" label="AIRTEL CG" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="glo_cg" label="GLO CG" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="mobile_cg" label="9MOBILE CG" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>

              {/* GIFTING */}

              <RHFSelect name="mtn_g" label="MTN GIFTING" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>

              <RHFSelect name="airtel_g" label="AIRTEL GIFTING" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="glo_g" label="GLO GIFTING" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="mobile_g" label="9MOBILE GIFTING" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>

              {/* SME 2 */}
              <RHFSelect name="mtn_sme2" label="MTN SME 2" defaultValue="Habukhan1">
                <option value="" />
                {allvend?.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="airtel_sme2" label="AIRTEL SME 2" defaultValue="Habukhan1">
                <option value="" />
                {allvend?.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="glo_sme2" label="GLO SME 2" defaultValue="Habukhan1">
                <option value="" />
                {allvend?.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="mobile_sme2" label="9MOBILE SME 2" defaultValue="Habukhan1">
                <option value="" />
                {allvend?.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>

              {/* DATASHARE */}
              <RHFSelect name="mtn_datashare" label="MTN DATASHARE" defaultValue="Habukhan1">
                <option value="" />
                {allvend?.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="airtel_datashare" label="AIRTEL DATASHARE" defaultValue="Habukhan1">
                <option value="" />
                {allvend?.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="glo_datashare" label="GLO DATASHARE" defaultValue="Habukhan1">
                <option value="" />
                {allvend?.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="mobile_datashare" label="9MOBILE DATASHARE" defaultValue="Habukhan1">
                <option value="" />
                {allvend?.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
